import React from 'react';
import { Box, Typography, List, ListItem, ListItemIcon, ListItemText } from '@material-ui/core';
import { FaFlagUsa, FaFlag } from 'react-icons/fa';

const languages = [
  {
    language: "Espaol",
    level: "Nativo",
    icon: <FaFlag style={{ color: 'green' }} /> // Puedes cambiar el color si lo prefieres
  },
  {
    language: "Ingls",
    level: "A2",
    icon: <FaFlagUsa style={{ color: 'blue' }} /> // Puedes cambiar el color si lo prefieres
  }
];

const Languages = () => (
  <Box mb={4} className="section" bgcolor="#f5f5f5" p={2} borderRadius={8}>
    <Typography variant="h3" color="primary" style={{ color: '#000' }}>Idiomas</Typography>
    <List>
      {languages.map((lang, index) => (
        <ListItem key={index}>
          <ListItemIcon>
            {lang.icon}
          </ListItemIcon>
          <ListItemText primary={`${lang.language}: ${lang.level}`} primaryTypographyProps={{ style: { color: '#000' } }} />
        </ListItem>
      ))}
    </List>
  </Box>
);

export default Languages;
